/* Classes */

input[type='text'].search,
select.search {
    font-family: Sans-Regular;
    font-size: 16px;
    color: var(--medium-grey);
    background-color: var(--lighter-grey);
    border: none;
    border-radius: var(--corners-small);
    margin: 0;
    padding: 5px;
}

input[type='text'].search {
    height: 25px;
    width: 83.75%;
}

select.search {
    height: 35px;
    width: 50%;
}

/* Mobile */

@media (max-width: 768px) {
    input[type='text'].search {
        width: 76%;
    }
}
